<template>
    <van-notice-bar
            left-icon="volume-o"
            text="向左划可以删除打包的数据"
    />
    <van-swipe-cell v-for="item of content" v-bind:key="item">
        <van-cell :border="false" :title="'打包师傅:'+item.packer" :value="item.created_at"  is-link @click="showPopup(item)"/>
        <template #right>
            <van-button square type="danger" text="删除"  @click="delPack(item)"/>
        </template>
    </van-swipe-cell>

    <van-popup v-model:show="show" position="right" :style="{ height: '100%',width:'85%' }">
        <item v-for="item in packData" v-bind:key="item"  :itemName="item.name" :product="item.stock"></item>
        <van-cell :title="'打包师傅：'+packer" :value="created_at"/>
    </van-popup>

</template>

<script>
    import item from "@/components/Item"
    import axios from 'axios'
    import router from '../router'
    export default {
        name: "check-pack-detail-count",
        data(){
            return{
                content:this.$store.state.checkPackDetailData,
                show:false,
                packer:"",
                packData:[],
                created_at:"",
            }
        },
        components:{
            item,
        },
        created(){

        },
        methods:{
            showPopup(item){
                this.packer = item.packer
                this.packData = item.pack
                this.created_at = item.created_at
                this.show = true
            },
            delPack(item){
                console.log("点击了删除按钮")
                console.log(item)
                axios.get(this.$store.state.domain+'del_pack_by_id',{
                    params:{
                        id:item.id
                    }
                }).then((res)=>{
                    if (res.status === 200){
                        router.back()
                    }
                })
            }
        },
    }
</script>

