<template>
    <div class="item-borde">
        <!--标题-->
        <van-row>
            <van-col span="3" style="font-weight: bold;font-size: 16px">型号</van-col>
            <van-col span="15" style="font-weight: bold;font-size: 16px">规格</van-col>
            <van-col span="6" style="font-size: 14px;font-weight: bold">总计：{{count}}</van-col>
        </van-row>
        <!--内容-->
        <van-row>
            <van-col span="3"><div style="margin-top: 50px;font-weight: bold">{{itemName}}</div></van-col>
            <van-col span="21">
                <!--详细内容-->
                <van-row class="mt20">
                    <van-col span="3">色</van-col>
                    <van-col span="3">S</van-col>
                    <van-col span="3">M</van-col>
                    <van-col span="3">L</van-col>
                    <van-col span="3">XL</van-col>
                    <van-col span="3">XXL</van-col>
                    <van-col span="3" offset="1">XXXL</van-col>
                </van-row>
                <van-row v-bind:key="item" v-for="item in data" class="mt20">
                    <van-col span="3">{{item[0].color}}</van-col>
                    <van-col :class="id" span="3">{{item[1].s}}</van-col>
                    <van-col :class="id" span="3">{{item[2].m}}</van-col>
                    <van-col :class="id" span="3">{{item[3].l}}</van-col>
                    <van-col :class="id" span="3">{{item[4].xl}}</van-col>
                    <van-col :class="id" span="3">{{item[5].xxl}}</van-col>
                    <van-col :class="id" span="3" offset="1">{{item[6].xxxl}}</van-col>
                </van-row>
            </van-col>
        </van-row>
    </div>
</template>

<script>
    // import $ from "jquery"
    export default {
        name: "item",
        data(){
            return{
                //每个item的独立ID.根据服务器返回的商品ID赋值
                id:this.itemID+"item",
                initCount:0,
                count:0,
                data:[]
            }
        },
        props:{'product':Array,'itemName':String,'itemID':Number},
        created(){
            this.data = this.product
        },
        mounted(){
            //挂载完页面，先统计一次总数
            this.countDown()
        },
        methods:{
            countDown(){
                var temp = 0;
                for (var j = 0;j<this.data.length;j++){
                    temp = temp+parseFloat(this.data[j][1].s)+
                        parseFloat(this.data[j][2].m)+
                        parseFloat(this.data[j][3].l)+
                        parseFloat(this.data[j][4].xl)+
                        parseFloat(this.data[j][5].xxl)+
                        parseFloat(this.data[j][6].xxxl)
                }
                this.count = temp;
            }
        }
    }
</script>

<style scoped>
    .mt20{
        margin-top: 20px;
    }
    .item-borde{
        border: 1px solid #ccc;
    }
</style>