<template>

    <item v-for="index in listData" v-bind:key="index"  :itemName="index.name" :product="index.stock"></item>

</template>

<script>
    import axios from "axios"
    import item from "@/components/Item"
    export default {
        name: "stock",
        data(){
            return{
                listData:[]
            }
        },
        components:{
            item,
        },
        setup() {
        },
        created(){
            axios.get(this.$store.state.domain+'product_get',{}).then((res)=>{
                this.listData = res.data
            })
        }
    }
</script>

