<template>
    <div style="width: 100%;text-align: center"><img alt="Vue logo" src="../assets/cyflogo.png"></div>
    <div class="content">
        <div class="longin-boder">
            <div class="image"><img src="../assets/user.png"></div>
            <input class="input" type="text" v-model="username" placeholder="输入用户名"/>
        </div>
        <!--End用户名输入框-->
        <div class="longin-boder">
            <div class="image"><img src="../assets/pwd.png"></div>
            <input class="input" type="password" v-model="password" placeholder="输入密码"/>
        </div>
        <div class="longin-boder">
            <div class="image"><img src="../assets/pwd.png"></div>
            <input class="input" type="password" v-model="password_two" placeholder="再次输入密码"/>
        </div>
        <!--End密码输入框-->
        <div class="button" v-on:click="onRegister()">创建此用户</div>
    </div>
</template>

<script>
    import axios from 'axios'
    import router from '../router'
    import {Toast} from "vant"
    export default {
        name: "register-user",
        data(){
            return{
                username:'',
                password:'',
                password_two:''
            }
        },
        created(){
            if (localStorage.getItem('username')!=="root"){
                router.replace("/")
            }
        },
        methods:{
            onRegister(){
                if (this.username===""){
                    Toast.fail("用户名不能为空")
                    return
                }
                if (this.username==="root"){
                    Toast.fail("不能创建root权限的账户")
                    return
                }
                if (this.password==="" || this.password_two===""){
                    Toast.fail("输入的密码不能为空")
                    return
                }
                if (this.password !== this.password_two){
                    Toast.fail("两次输入的密码不相同")
                    return
                }
                axios.get(this.$store.state.domain+"user_add",{
                    params:{
                        username:this.username,
                        password:this.password
                    }
                }).then((res)=>{
                    if (res.status===200){
                        Toast.success(res.data)
                        router.replace('/setting')
                    }
                })
            }
        }
    }
</script>

<style scoped>

    .content{
        width: 100%;
        margin-top: 40%;
        text-align: center;
    }
    .longin-boder{
        width: 80%;
        height: 40px;
        margin-top: 10px;
        margin-left: 10%;
        line-height: 40px;
        text-align: center;
        border: 1px solid #dddddd;
        border-radius: 5px;
        background-color: #efefef;
    }
    .image{
        width: 10%;
        margin-top: 5px;
        float: left;
        text-align: right;
    }
    .input{
        width: 80%;
        float: left;
        margin-left: 5%;
        height: 37px;
        line-height: 37px;
        border:0px;
        color: #333333;
        font-size: 16px;
        background-color: #efefef;

    }

    .button{
        height: 40px;
        width: 80%;
        margin-top: 20px;
        margin-left: 10%;
        font-size: 16px;
        font-family: "微软雅黑";
        font-weight: bold;
        line-height: 38px;
        border-radius: 5px;
        color: #ffffff;
        line-height: 45px;
        background-color: #37B4CA;

    }

    .button:active {
        background-color:#37a3b9;
    }
    /*.input-wrap input{*/
    /*height: 26px;*/
    /*border-radius: 5px;*/
    /*}*/
</style>