<template>
    <item v-for="item of outstandingProduct" v-bind:key="item"
          :product="item.stock"
          :itemName="item.name"
          :itemID="item.id"
    ></item>
    <van-cell-group>
        <van-cell title="客户名字" :value="orderData.customer" />
        <van-cell title="目的地" :value="orderData.destination" />
        <van-cell title="备注" :value="orderData.remark" />
    </van-cell-group>
    <van-button :loading="packout_order" :disabled="packout_order" loading-text="正在打包出库中..." type="success" size="large" @click="packOut()">打包出库</van-button>
</template>

<script>
    import item from '@/components/Item'
    import router from '../router'
    import axios from 'axios'
    import { Toast } from 'vant'
    export default {
        name: "pack-done",
        data(){
            return{
                outstandingProduct:this.$store.state.outstandingProduct,
                orderData:this.$store.state.orderData,
                oustandingStatus:0,
                packProduct:this.$store.state.PackProduct,
                outstandingPage:0,
                packout_order:false
            }
        },
        created(){
            this.outstandingPage = this.$route.query.outstandingstatus
            if (this.outstandingProduct.length===0){
                router.replace('/')
            }
        },
        components:{
            item
        },
        methods:{
            packOut(){
                this.outstandingProduct.forEach((item)=>{
                    if (item.count !== 0 ){
                        this.oustandingStatus = 1
                    }
                })
                this.postPackOut()
            },
            postPackOut(){
                this.Loading = true;
                this.packout_order = true
                if (this.outstandingPage === "0"){
                    axios.get(this.$store.state.domain+'order_pack_out',{
                        params:{
                            packProduct:this.packProduct,
                            oustandingStatus:this.oustandingStatus,
                            orderID:this.orderData.id,
                            packer:localStorage.getItem('username'),
                            outstanding:this.outstandingProduct,
                        }
                    }).then((res)=>{
                        if (res.status === 200){
                            router.replace('/')
                            Toast("打包完成")
                            this.packout_order = false
                        }else{
                            Toast.fail(res.data)
                        }
                    })
                }else if(this.outstandingPage === "1"){
                    axios.get(this.$store.state.domain+'outstanding_order_pack_out',{
                        params:{
                            packProduct:this.packProduct,
                            oustandingStatus:this.oustandingStatus,
                            orderID:this.orderData.id,
                            packer:localStorage.getItem('username'),
                            outstanding:this.outstandingProduct,
                        }
                    }).then((res)=>{
                        if (res.status === 200){
                            router.replace('/')
                            Toast("打包完成")
                            this.packout_order = false
                        }else{
                            Toast.fail(res.data)
                        }
                    })
                }
            }
        }
    }
</script>

