<template>
    <van-loading v-if="Loading" size="24px" color="#0094ff" vertical>加载中...</van-loading>
    <div v-else>
        <van-tabs active="active" @click="onTabsClick">
            <van-tab v-for="item of packProducts" v-bind:key="item" :title="item.name" :badge="item.count"></van-tab>
        </van-tabs>
        <div class="pack_title">需要打包</div>
        <item v-for="product of products" v-bind:key="product"
              v-show="product.name===now_product?true:false"
              :product="product.stock"
              :itemName="product.name"
              :itemID="product.id"
        ></item>
        <div class="pack_title">已打包</div>
        <packitem v-for="packProduct of packProducts" v-bind:key="packProduct"
                  v-show="packProduct.name===now_product?true:false"
                  :detailData="packProduct.stock"
                  :itemName="packProduct.name"
                  :itemId="packProduct.id"></packitem>
        <van-button type="success" size="large" @click="PackDone()">下一步</van-button>
    </div>
</template>

<script>
    import packitem from '@/components/PackItem'
    import item from '@/components/Item'
    import router from '../router'
    import axios from 'axios'
    import {Toast} from 'vant'
    export default {
        name: "pack",
        // props:{'products':Array},
        data(){
            return{
                orderId:this.$route.params.orderid,
                data:[],
                packProducts:[],
                products:[],
                outstandingProduct:[],
                show:false,
                outstandingstatus:0,
                Loading:true,
                now_product:"",
            }
        },
        components:{
            packitem,
            item
        },
        created(){
            //先判断如果是欠货，设定欠货状态为1
            if (this.$route.params.outstanding==="true"){
                this.outstandingstatus = 1
            }
            axios.get(this.$store.state.domain+'order_get_by_id_set_status',{
                params:{
                    orderID:this.orderId,
                    status:1
                }
            }).then((res)=>{
                if (this.outstandingstatus === 1){
                    this.products = JSON.parse(JSON.stringify(res.data.outstanding))
                    this.$store.state.shouldPackProduct = JSON.parse(JSON.stringify(res.data.outstanding))
                }else{
                    this.products = JSON.parse(JSON.stringify(res.data.content))
                    this.$store.state.shouldPackProduct = JSON.parse(JSON.stringify(res.data.content))
                }
                this.outstandingProduct = JSON.parse(JSON.stringify(res.data.content))
                this.$store.state.orderData = JSON.parse(JSON.stringify(res.data))
                this.$store.state.PackProduct = JSON.parse(JSON.stringify(res.data.content))
                this.packProducts = this.$store.state.PackProduct
                this.now_product = this.packProducts[0].name
                this.$store.state.outstandingProduct = JSON.parse(JSON.stringify(res.data.content))
                this.Loading = false
            })
        },
        methods:{
            onTabsClick(name, title){
                Toast('正在打包'+title)
                this.now_product = title
            },
            PackDone(){
                for (var i = 0;i < this.outstandingProduct.length;i++){
                    this.outstandingProduct[i].stock = this.arrDel(this.packProducts[i].stock,this.products[i].stock)
                    this.outstandingProduct[i].count = this.products[i].count - this.$store.state.PackProduct[i].count
                }
                this.$store.commit('packDone',this.outstandingProduct)
                router.push({path:'/pack/done',query:{outstandingstatus:this.outstandingstatus}})
            },
            arrDel(arr1,arr2){
                var arr = JSON.parse(JSON.stringify(arr1))
                for (var i = 0;i < arr1.length;i++){
                            arr[i][1].s = arr2[i][1].s - arr1[i][1].s
                            arr[i][2].m = arr2[i][2].m - arr1[i][2].m
                            arr[i][3].l = arr2[i][3].l-arr1[i][3].l
                            arr[i][4].xl = arr2[i][4].xl-arr1[i][4].xl
                            arr[i][5].xxl = arr2[i][5].xxl-arr1[i][5].xxl
                            arr[i][6].xxxl = arr2[i][6].xxxl-arr1[i][6].xxxl
                }
                return arr
            },
            arrAdd(arr1,arr2){
                var arr = JSON.parse(JSON.stringify(arr1))
                for (var i = 0;i < arr1.length;i++){
                    arr[i][1].s = arr2[i][1].s + arr1[i][1].s
                    arr[i][2].m = arr2[i][2].m + arr1[i][2].m
                    arr[i][3].l = arr2[i][3].l + arr1[i][3].l
                    arr[i][4].xl = arr2[i][4].xl + arr1[i][4].xl
                    arr[i][5].xxl = arr2[i][5].xxl + arr1[i][5].xxl
                    arr[i][6].xxxl = arr2[i][6].xxxl + arr1[i][6].xxxl
                }
                return arr
            }
        }
    }
</script>

<style scoped>
    .pack_title{
        width: 100%;
        height: 36px;
        line-height: 36px;
        font-size: 24px;
        color: brown;
        text-align: center;
    }
    .pop_style{
        margin-top: 30px;
    }
</style>