<template>
    <div style="width: 100%;text-align: center"><img alt="Vue logo" src="../assets/cyflogo.png"></div>
    <div class="content">
        <div class="longin-boder">
            <div class="image"><img src="../assets/user.png"></div>
            <input class="input" type="text" v-model="username" placeholder="输入用户名"/>
        </div>
        <!--End用户名输入框-->
        <div class="longin-boder">
            <div class="image"><img src="../assets/pwd.png"></div>
            <input class="input" type="password" v-model="password" placeholder="输入密码"/>
        </div>
        <!--End密码输入框-->
        <div class="button" v-on:click="onLogin()">登录</div>
    </div>
</template>

<script>
    import axios from 'axios'
    import router from "../router"
    import {Toast} from "vant"
    // import console from 'vconsole'
    export default {
        name: "login",
        data(){
            return{
                username:'',
                password:''
            }
        },
        created(){
            this.checkLogin()
            console.log('OnCreated')
            // console.log(localStorage.getItem('isLogin'))
        },
        methods:{
            checkLogin(){
                if(localStorage.getItem('isLogin')){
                    router.replace('/')
                }
            },
            onLogin(){
                axios.get(this.$store.state.domain+'login',{
                    params: {
                        username:this.username,
                        password:this.password
                    }
                }).then((res)=>{
                    console.log(res)
                    if (res.data===true){
                        localStorage.setItem('isLogin',res.data)
                        localStorage.setItem('username',this.username)
                        Toast.success("登陆成功")
                        router.replace('/')
                    }else{
                        Toast.fail("登陆失败！"+res.data)
                    }
                })
            }
        },
    }
</script>

<style scoped>

    .content{
        width: 100%;
        margin-top: 40%;
        text-align: center;
    }
    .longin-boder{
        width: 80%;
        height: 40px;
        margin-top: 10px;
        margin-left: 10%;
        line-height: 40px;
        text-align: center;
        border: 1px solid #dddddd;
        border-radius: 5px;
        background-color: #efefef;
    }
    .image{
        width: 10%;
        margin-top: 5px;
        float: left;
        text-align: right;
    }
    .input{
        width: 80%;
        float: left;
        margin-left: 5%;
        height: 37px;
        line-height: 37px;
        border:0px;
        color: #333333;
        font-size: 16px;
        background-color: #efefef;

    }

    .button{
        height: 40px;
        width: 80%;
        margin-top: 20px;
        margin-left: 10%;
        font-size: 16px;
        font-family: "微软雅黑";
        font-weight: bold;
        line-height: 38px;
        border-radius: 5px;
        color: #ffffff;
        line-height: 45px;
        background-color: #37B4CA;

    }

    .button:active {
        background-color:#37a3b9;
    }
    /*.input-wrap input{*/
        /*height: 26px;*/
        /*border-radius: 5px;*/
    /*}*/
</style>