// import { createStore } from 'vuex'
import { createStore } from 'vuex'




const store = createStore({
    //数据
    state(){
        return{
            domain:'http://7ipkuq.natappfree.cc -> 127.0.0.1:80',
            // domain:'http://192.168.7.230:8080/',s
            //订单总数量详细页面数据
            checkDetailData:[],
            //订单欠货数量详细页面数据
            checkOsDetailData:[],
            //订单打包的详细页面数据
            checkPackDetailData:[],
            orderProduct:[],
            orderProductCount:[],
            orderItem:[],
            shouldPackProduct:[],
            PackProduct:[],
            outstandingProduct:[],
            orderData:[],
            EditProducts:[],
            stockProduct:[],
        }
    },
    //使用store.commit(),提交的方法
    mutations:{
        /**
         * 添加订单时候，添加产品
         * @param state
         * @param product
         */
        orderProductChange(state,product){
            if (state.orderProduct.length===0){
                state.orderProduct.push(product)
                return
            }
            var item = state.orderProduct.find(item=>item.name===product.name)
            if (item===undefined){
                state.orderProduct.push(product)
                return
            }
            state.orderProduct.forEach((item)=>{
                if (item.name === product.name){
                    item.stock = product.stock
                    item.count = product.count
                }
            })
        },
        orderProductItem(state,shouldDel){
            console.log(shouldDel)
            if (shouldDel===0){
                state.orderProduct = []
                return
            }
            state.orderProduct.forEach((item,index)=>{
                if (item.name === shouldDel){
                    state.orderProduct.splice(index,1)
                }
            })
        },
        packProductChange(state,data){
            for(var i = 0;i < state.PackProduct.length;i++){
                if (state.PackProduct[i].name === data.name){
                    state.PackProduct[i].stock = data.stock
                    state.PackProduct[i].count = data.count
                }
            }
        },
        packDone(state,data){
            state.outstandingProduct = data
        },
        stockProductChange(state,data){
            for(var i = 0;i < state.stockProduct.length;i++){
                if (state.stockProduct[i].name === data.name){
                    state.stockProduct[i].stock = data.stock
                    state.stockProduct[i].count = data.count
                }
            }
        },
        setDetailData(state,data){
            state.checkDetailData = data
        },
        setOsDetailData(state,data){
            state.checkOsDetailData = data
        },
        setPacksDetailData(state,data){
            state.checkPackDetailData = data
        }
    }
})

export default store