<template>
    <div class="item-borde">
        <!--标题-->
        <van-row>
            <van-col span="3" style="font-weight: bold;font-size: 16px">型号</van-col>
            <van-col span="15" style="font-weight: bold;font-size: 16px">规格</van-col>
            <van-col span="6" style="font-size: 14px;font-weight: bold">总计：{{count}}</van-col>
        </van-row>
        <!--内容-->
        <van-row>
            <van-col span="3">
                <div style="margin-top: 50px;font-weight: bold">
                    <input class="cc-input name" v-model="name" type="text" placeholder="型号">
                </div>
            </van-col>
            <van-col id="content" span="21">
                <!--详细内容-->
                <van-row class="mt20">
                    <van-col span="3">色</van-col>
                    <van-col span="3">S</van-col>
                    <van-col span="3">M</van-col>
                    <van-col span="3">L</van-col>
                    <van-col span="3">XL</van-col>
                    <van-col span="3">XXL</van-col>
                    <van-col span="3" offset="1">XXXL</van-col>
                </van-row>
                <van-row v-bind:key="item" v-for="(item,index) in itemInput" class="mt20">
                    <van-col :class="index+'color'" class="cc-input" span="3"><input class="cc-input" type="text" v-model="itemInput[index][0].color"></van-col>
                    <van-col span="3"><input v-model="itemInput[index][1].s" class="cc-input" @change="countDown()" type="number" :placeholder="item[1].s"></van-col>
                    <van-col span="3"><input v-model="itemInput[index][2].m" class="cc-input" @change="countDown()" type="number" :placeholder="item[2].m"></van-col>
                    <van-col span="3"><input v-model="itemInput[index][3].l" class="cc-input" @change="countDown()" type="number" :placeholder="item[3].l"></van-col>
                    <van-col span="3"><input v-model="itemInput[index][4].xl" class="cc-input" @change="countDown()" type="number" :placeholder="item[4].xl"></van-col>
                    <van-col span="3"><input v-model="itemInput[index][5].xxl" class="cc-input" @change="countDown()" type="number" :placeholder="item[5].xxl"></van-col>
                    <van-col span="3"><input v-model="itemInput[index][6].xxxl" class="cc-input" @change="countDown()" type="number" :placeholder="item[6].xxxl"></van-col>
                </van-row>
            </van-col>
        </van-row>
        <van-row>
            <van-button @click="addColor()" style="margin-top: 20px" size="large" type="success">添加颜色</van-button>
        </van-row>
        <van-row>
            <van-button @click="addProduct()" style="margin-top: 20px" size="large" type="primary">上传商品</van-button>
        </van-row>
    </div>
</template>

<script>
    import $ from "jquery"
    import axios from "axios"
    import {Toast} from "vant"
    import router from '../router'
    export default {
        name: "product-add",
        data(){
            return{
                count:0,
                name:"",
                content:[{color:""},{s:0},{m:0},{l:0},{xl:0},{xxl:0},{xxxl:0}],
                itemInput:[[{color:""},{s:0},{m:0},{l:0},{xl:0},{xxl:0},{xxxl:0}]],
            }
        },
        props:{'detailData':Array},
        created(){
            console.log(this.$router)
            // console.log(this.detailData)

        },
        mounted(){
            //挂载完页面，先统计一次总数
            this.countDown()
        },
        methods:{
            countDown(){
                var temp = 0;
                for (var j = 0;j<this.itemInput.length;j++){
                    temp = temp+Number(parseFloat(this.itemInput[j][1].s))+
                        Number(parseFloat(this.itemInput[j][2].m))+
                        Number(parseFloat(this.itemInput[j][3].l))+
                        Number(parseFloat(this.itemInput[j][4].xl))+
                        Number(parseFloat(this.itemInput[j][5].xxl))+
                        Number(parseFloat(this.itemInput[j][6].xxxl))
                }
                this.count = temp;
            },
            addColor(){
                this.itemInput.push(JSON.parse(JSON.stringify(this.content)))
            },
            addProduct(){
                if (this.name===""){
                    Toast("型号不能为空")
                }
                $(".color").each(function (index,arg) {
                    if (arg.value===""){
                        Toast("颜色不能为空")
                    }
                });
                console.log(this.itemInput)
                axios.get(this.$store.state.domain+'product_add',{
                    params: {
                        name:this.name,
                        stock:this.itemInput
                    }
                }).then((res)=>{
                    if (res.status===200){
                        router.back()
                    }

                })
            }
        }
    }
</script>

<style scoped>
    .mt20{
        margin-top: 20px;
    }
    .item-borde{
        border: 1px solid #ccc;
    }
    .cc-input{
        border-radius: 5px;
        appearance: none;
        -webkit-appearance: none !important;
        -moz-appearance: textfield !important;
        text-align: center;
        width: 100%;
    }
    .color-wrap{
        font-size: 16px;
    }
</style>