<template>
    <tabbar :ifactive="3"></tabbar>


    <van-divider content-position="left">商品相关的操作</van-divider>
    <van-cell-group>
        <van-cell title=添加商品 is-link to="/productadd" />
        <van-cell title=查看库存 is-link to="/stock" />
        <van-cell title=编辑库存 is-link to="/stockedit" />
        <van-cell title=删除库存商品 is-link to="/delstock" />
    </van-cell-group>


    <van-divider content-position="left">订单相关的操作</van-divider>
    <van-cell-group>
        <van-cell title=撤销订单打包状态 is-link to="/pack/status/change" />
    </van-cell-group>

    <van-divider content-position="left">用户相关的操作</van-divider>
    <van-cell-group>
        <van-cell title=退出登陆 is-link @click="logout()" />
        <van-cell v-if="show" title=创建用户 is-link to="/register" />
    </van-cell-group>


</template>

<script>
    import tabbar from '@/components/Tabbar'
    import router from '../router'
    import {Dialog} from 'vant'
    import {Toast} from 'vant'
    export default {
        name: "setting",
        data(){
            return{
                username:localStorage.getItem('username'),
                show:false
            }
        },
        created(){
            if (localStorage.getItem('username')==="root"){
                this.show = true
            }
        },
        components:{
            tabbar
        },
        methods:{
            logout(){
                Dialog.confirm({
                    title: '要退出登陆吗？',
                })
                    .then(() => {
                        // on confirm
                        localStorage.removeItem('isLogin')
                        localStorage.removeItem('username')
                        router.replace('/login')
                    })
                    .catch(() => {
                        // on cancel
                        Toast('取消退出登陆')
                    });
            },
            createdUser(){

            }
        }
    }
</script>

<style scoped>

</style>