<template>
    <item v-for="item in content" v-bind:key="item"  :itemName="item.name" :product="item.stock"></item>
</template>

<script>
    import item from "@/components/Item"
    export default {
        name: "check-detail-count",
        data(){
            return{
                from:"",
                content:[]
            }
        },
        components:{
            item,
        },
        created(){
            this.from = this.$route.query.from
            switch (this.from){
                case "count":
                    this.content = this.$store.state.checkDetailData
                    break
                case "oscount":
                    this.content = this.$store.state.checkOsDetailData
                    break
                default:
                    return
            }
        }
    }
</script>

