<template>
    <div class="item-borde">
        <!--标题-->
        <van-row>
            <van-col span="3" style="font-weight: bold;font-size: 16px">型号</van-col>
            <van-col span="15" style="font-weight: bold;font-size: 16px">规格</van-col>
            <van-col span="6" style="font-size: 14px;font-weight: bold">总计：<span :id="itemId+'count'">{{count}}</span></van-col>
        </van-row>
        <!--内容-->
        <van-row>
            <van-col span="3"><div :class="itemId+'name'" style="margin-top: 50px;font-weight: bold">{{itemName}}</div></van-col>
            <van-col span="21">
                <!--详细内容-->
                <van-row class="mt20">
                    <van-col span="3">色</van-col>
                    <van-col span="3">S</van-col>
                    <van-col span="3">M</van-col>
                    <van-col span="3">L</van-col>
                    <van-col span="3">XL</van-col>
                    <van-col span="3">XXL</van-col>
                    <van-col span="3" offset="1">XXXL</van-col>
                </van-row>
                <van-row v-bind:key="item" v-for="(item,index) in detailData" class="mt20">
                    <van-col :class="index+'color'" span="3">{{item[0].color}}</van-col>
                    <van-col span="3"><input @click="defaultVal(index,1)" @blur="countDown()" v-model="itemInput[index][1].s" class="cc-input"  type="number" :placeholder="item[1].s"></van-col>
                    <van-col span="3"><input @click="defaultVal(index,2)" @blur="countDown()" v-model="itemInput[index][2].m" class="cc-input"  type="number" :placeholder="item[2].m"></van-col>
                    <van-col span="3"><input @click="defaultVal(index,3)" @blur="countDown()" v-model="itemInput[index][3].l" class="cc-input"  type="number" :placeholder="item[3].l"></van-col>
                    <van-col span="3"><input @click="defaultVal(index,4)" @blur="countDown()" v-model="itemInput[index][4].xl" class="cc-input"  type="number" :placeholder="item[4].xl"></van-col>
                    <van-col span="3"><input @click="defaultVal(index,5)" @blur="countDown()" v-model="itemInput[index][5].xxl" class="cc-input"  type="number" :placeholder="item[5].xxl"></van-col>
                    <van-col span="3"><input @click="defaultVal(index,6)" @blur="countDown()" v-model="itemInput[index][6].xxxl" class="cc-input"  type="number" :placeholder="item[6].xxxl"></van-col>
                </van-row>
            </van-col>
        </van-row>
    </div>
</template>

<script>
    export default {
        name: "order-add-item",
        data(){
            return{
                //每个item的独立ID.根据服务器返回的商品ID赋值
                id:this.itemId,
                count:0,
                itemInput:[],
            }
        },
        props:{'detailData':Array,"itemName":String,"itemId":Number},
        created(){
            var temp = this.detailData
            for (var i = 0;i<this.detailData.length;i++){
                // temp[i].color = this.detailData[i].color
                temp[i][1].s = 0
                temp[i][2].m = 0
                temp[i][3].l = 0
                temp[i][4].xl = 0
                temp[i][5].xxl = 0
                temp[i][6].xxxl = 0
            }
            this.itemInput = temp
        },
        mounted(){
            //挂载完页面，先统计一次总数
            this.countDown()
        },
        methods:{
            countDown(){
                var temp = 0;
                for (var j = 0;j<this.itemInput.length;j++){
                    this.itemInput[j][1].s = this.toZero(this.itemInput[j][1].s)
                    this.itemInput[j][2].m = this.toZero(this.itemInput[j][2].m)
                    this.itemInput[j][3].l = this.toZero(this.itemInput[j][3].l)
                    this.itemInput[j][4].xl = this.toZero(this.itemInput[j][4].xl)
                    this.itemInput[j][5].xxl = this.toZero(this.itemInput[j][5].xxl)
                    this.itemInput[j][6].xxxl = this.toZero(this.itemInput[j][6].xxxl)
                    temp = temp+parseFloat(this.itemInput[j][1].s)+
                        parseFloat(this.itemInput[j][2].m)+
                        parseFloat(this.itemInput[j][3].l)+
                        parseFloat(this.itemInput[j][4].xl)+
                        parseFloat(this.itemInput[j][5].xxl)+
                        parseFloat(this.itemInput[j][6].xxxl)
                }
                this.count = temp;
                var data = {name:this.itemName,stock:this.itemInput,count:this.count,id:this.itemId}
                this.$store.commit('orderProductChange',data)
            },
            toZero(val){
                if (isNaN(parseFloat(val))){
                    return 0
                }
                return val
            },
            defaultVal(index,col){
                switch (col){
                    case 1:
                        this.itemInput[index][col].s=""
                        break
                    case 2:
                        this.itemInput[index][col].m=""
                        break
                    case 3:
                        this.itemInput[index][col].l=""
                        break
                    case 4:
                        this.itemInput[index][col].xl=""
                        break
                    case 5:
                        this.itemInput[index][col].xxl=""
                        break
                    case 6:
                        this.itemInput[index][col].xxxl=""
                        break
                    default:
                        return
                }

            }
        }
    }
</script>

<style scoped>
    .mt20{
        margin-top: 20px;
        padding-bottom: 20px;
    }
    .item-borde{
        border: 1px solid #ccc;
    }
    .cc-input{
        border-radius: 5px;
        appearance: none;
        -webkit-appearance: none !important;
        -moz-appearance: textfield !important;
        text-align: center;
        width: 100%;
        color: brown;
    }
    input::placeholder{
        color: #c0c0c0;
    }
</style>