<template>
    <div class="order-wrap">
    <van-cell-group>
        <van-cell title="创建时间" :value=created_at />
        <van-cell title="客户" :value=customer />
        <van-cell title="订单状态" :value=status_zh />
        <van-cell title="送往" :value=destination />
    </van-cell-group>
        <van-cell value="备注" />
        <div class="remark_wrap"><span class="remark_content">{{remark}}</span></div>
        <van-cell title="总数量"  value="点击查看详细"  @click="goDetail()"/>
        <div class="remark_wrap1" @click="goDetail()">
            <van-row  v-for="item of content" v-bind:key="item" style="margin: 15px 0px">
                <van-col span="12">{{item.name}}</van-col>
                <van-col span="12">{{item.count}}件</van-col>
            </van-row>
        </div>
        <div v-if="show_os">
            <van-cell title="欠货数量" value="点击查看详细"  @click="goOsDetail()"/>
            <div @click="goOsDetail()" class="remark_wrap1">
                <van-row v-for="item of outstanding" v-bind:key="item">
                    <van-col span="12">{{item.name}}</van-col>
                    <van-col span="12">{{item.count}}件</van-col>
                </van-row>
            </div>
        </div>
        <div v-if="show_pack">
            <van-cell title="打包详情" value="点击查看详细" @click="goPacksDetail()" />
            <van-row v-for="(pack,index) of packData" v-bind:key="pack" style="margin-bottom: 10px;text-align: center">
                <van-col span="6" ><span style="font-size: 12px">第{{index+1}}次打包:</span></van-col>
                <van-col span="6"><span style="font-size: 12px">{{pack.packer}}</span></van-col>
                <van-col span="12" style="font-size: 16px">
                    <van-row v-for="child of pack.pack" v-bind:key="child">
                        <van-col>编号：{{child.name}}共{{child.count}}件</van-col>
                    </van-row>
                </van-col>
            </van-row>
        </div>
    <van-row style="text-align: center">
    <van-col span="8">
    <van-button size="small" type="primary" @click="packing()" :disabled="show_packing">点击打包</van-button>
    </van-col>
    <van-col span="8">
    <van-button size="small" type="warning" @click="edit()" :disabled="show_packing">重新编辑</van-button>
    </van-col>
    <van-col span="8">
    <van-button size="small" @click="deleteOrder()" type="danger">删除订单</van-button>
    </van-col>
    </van-row>
    </div>
</template>

<script>
    import {Dialog} from 'vant'
    import {Toast} from 'vant'
    import axios from 'axios'
    import router from '../router'
    export default {
        name: "order-item",
        props:{'created_at':String,
            'customer':String,
            'content':Array,
            'remark':String,
            'destination':String,
            'status':Number,
            'orderId':Number,
            'outstanding':Array,
            'packData':Array,
            'page':String,
            'show_os':Boolean,
            'show_pack':Boolean,
            'show_packing':Boolean,
        },
        data(){
            return{
                status_zh:"",
                if_pack:false,
                detail:false,
            }
        },

        created(){
            switch (this.status){
                //新订单打包
                case 0:
                    this.status_zh = "等待打包"
                    this.if_pack = false
                    break;
                //打包中
                case 1:
                    this.status_zh = "打包中"
                    this.if_pack = true
                    break;
                //欠货订单等待打包
                case 2:
                    this.status_zh = "等待打包"
                    this.if_pack = false
                    break;
                default:
                    return
            }
            if (this.page==="outstanding"||this.page==="completed"){
                this.detail = true
            }else {
                this.detail = false
            }
        },
        mounted(){
        },
        methods:{
            packing(){
                axios.get(this.$store.state.domain+'order_get_status',{
                    params:{
                        orderID:this.orderId
                    }
                }).then((res)=>{
                    //0：新建的订单
                    if (res.data===0){
                        router.push('/pack/'+this.orderId+"/false")
                    //1：正在打包中
                    }else if (res.data===1){
                        Toast.fail('该订单已经在打包中，请重新刷新页面')
                    }
                    //2：上一次打包完还欠货的订单状态
                    else if (res.data===2){
                        router.push('/pack/'+this.orderId+"/true")
                    //其他的不管了
                    }else{
                        Toast.fail('该订单已经打包完成，请重新刷新页面')
                    }
                })

            },
            deleteOrder(){
                Dialog.confirm({
                    title: '确定删除订单吗？',
                    message: '删除订单后不可找回',
                })
                    .then(() => {
                        // on confirm
                        axios.get(this.$store.state.domain+'order_del_by_id',{
                            params:{
                                orderID:this.orderId
                            }
                        }).then((res)=>{
                            if (res.status===200){
                                Toast.success('删除成功')
                                router.go(0)
                            }else {
                                Toast.fail(res.data)
                            }
                        })

                    })
                    .catch(() => {
                        // on cancel
                        Toast('取消删除')
                    });
            },
            edit(){
                router.push('/orderedit/'+this.orderId)
            },
            goDetail(){
                // console.log(this.content)
                this.$store.commit('setDetailData',this.content)
                router.push({path:'/check_detail_count',query:{from:"count"}})
            },
            goOsDetail(){
                // console.log(this.content)
                this.$store.commit('setOsDetailData',this.outstanding)
                router.push({path:'/check_detail_count',query:{from:"oscount"}})
            },
            goPacksDetail(){
                this.$store.commit('setPacksDetailData',this.packData)
                router.push({path:'/check_pack_detail_count'})
            }
        }
    }
</script>

<style scoped>
    .order-wrap{
        margin: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid #ccc;
    }
    .remark_wrap{
        height: 60px;
        margin: 5px 30px;
        background-color: #ddd;
        border-radius: 5px;
    }
    .remark_wrap1{
        margin: 30px 30px;
        background-color: #ddd;
        border-radius: 5px;
    }
    .remark_content{
        margin: 10px;
        color:#696969;
    }

</style>