<template>
    <div class="item-borde">
        <!--标题-->
        <van-row>
            <van-col span="3" style="font-weight: bold;font-size: 16px;text-align: center">型号</van-col>
            <van-col span="15" style="font-weight: bold;font-size: 16px;text-align: center">规格</van-col>
            <van-col span="6" style="font-size: 14px;font-weight: bold;text-align: center">总计：{{count}}</van-col>
        </van-row>
        <!--内容-->
        <van-row>
            <van-col span="3"><div style="margin-top: 50px;font-weight: bold;text-align: center">{{itemName}}</div></van-col>
            <van-col span="21">
                <!--详细内容-->
                <van-row class="mt20">
                    <van-col class="col" span="3">色</van-col>
                    <van-col class="col" span="3">S</van-col>
                    <van-col class="col" span="3">M</van-col>
                    <van-col class="col" span="3">L</van-col>
                    <van-col class="col" span="3">XL</van-col>
                    <van-col class="col" span="3">XXL</van-col>
                    <van-col class="col" span="3" offset="1">XXXL</van-col>
                </van-row>
                <van-row v-bind:key="item" v-for="(item,index) in detailData" class="mt20">
                    <van-col class="col" :class="index+'color'" span="3">{{item[0].color}}</van-col>
                    <van-col span="3"><input v-model="itemInput[index][1].s" class="cc-input" @change="countDown()" type="number" :placeholder="item[1].s"></van-col>
                    <van-col span="3"><input v-model="itemInput[index][2].m" class="cc-input" @change="countDown()" type="number" :placeholder="item[2].m"></van-col>
                    <van-col span="3"><input v-model="itemInput[index][3].l" class="cc-input" @change="countDown()" type="number" :placeholder="item[3].l"></van-col>
                    <van-col span="3"><input v-model="itemInput[index][4].xl" class="cc-input" @change="countDown()" type="number" :placeholder="item[4].xl"></van-col>
                    <van-col span="3"><input v-model="itemInput[index][5].xxl" class="cc-input" @change="countDown()" type="number" :placeholder="item[5].xxl"></van-col>
                    <van-col span="3"><input v-model="itemInput[index][6].xxxl" class="cc-input" @change="countDown()" type="number" :placeholder="item[6].xxxl"></van-col>
                </van-row>
            </van-col>
        </van-row>
    </div>
</template>

<script>
    export default {
        name: "stock-item",
        data(){
            return{
                id:this.itemId,
                count:0,
                itemInput:[],
            }
        },
        props:{'detailData':Array,'itemName':String,"itemId":Number},
        created(){
            this.itemInput = this.detailData
        },
        mounted(){
            //挂载完页面，先统计一次总数
            this.countDown()
        },
        methods:{
            countDown(){
                var temp = 0;
                for (var j = 0;j<this.itemInput.length;j++){
                    temp = temp+Number(parseFloat(this.itemInput[j][1].s))+
                        Number(parseFloat(this.itemInput[j][2].m))+
                        Number(parseFloat(this.itemInput[j][3].l))+
                        Number(parseFloat(this.itemInput[j][4].xl))+
                        Number(parseFloat(this.itemInput[j][5].xxl))+
                        Number(parseFloat(this.itemInput[j][6].xxxl))
                }
                this.count = temp;
                var data = {name:this.itemName,stock:this.itemInput,count:this.count,id:this.itemId}
                this.$store.commit('stockProductChange',data)
            }
        }
    }
</script>

<style scoped>
    .mt20{
        margin-top: 20px;
        padding-bottom: 20px;
    }
    .item-borde{
        border: 1px solid #ccc;
    }
    .cc-input{
        border-radius: 5px;
        appearance: none;
        -webkit-appearance: none !important;
        -moz-appearance: textfield !important;
        text-align: center;
        width: 100%;
    }
    .col{
        text-align: center;
    }
</style>