<template>
    <tabbar :ifactive="1"></tabbar>
    <van-loading v-if="Loading" size="24px" color="#0094ff" vertical>加载中...</van-loading>
    <div v-else-if="orders.length===0 && !Loading" style="height: 100%;text-align: center;line-height: 400px;font-size: 30px;font-weight: bold">当前没有欠货订单</div>
    <div v-else>
        <van-cell v-for="item in orders" :key="item" :title="'客户:'+item.customer" is-link :value=item.created_at :label="'订单号:'+item.uuid"  @click="showPopup(item)"/>
        <van-popup v-model:show="show" position="right" :style="{ height: '100%',width:'85%' }">
            <orderitem
                    :created_at="nowOrder.created_at"
                    :customer="nowOrder.customer"
                    :content="nowOrder.content"
                    :remark="nowOrder.remark"
                    :destination="nowOrder.destination"
                    :status="nowOrder.status"
                    :orderId="nowOrder.id"
                    :outstanding="nowOrder.outstanding"
                    :packData="packData"
                    :page="page"
                    :show_os="true"
                    :show_pack="true"
                    :show_packing="nowOrder.status==1?true:false"
            ></orderitem>
        </van-popup>
    </div>
</template>

<script>
    import axios from 'axios'
    import tabbar from '@/components/Tabbar'
    import orderitem from "@/components/OrderItem.vue"
    // import outstandingitem from "@/components/OutstandingItem.vue"
    export default {
        name: "outstanding-order",
        data(){
            return{
                orders:[],
                show:false,
                nowOrder:[],
                packData:[],
                page:"outstanding",
                Loading:true,
            }
        },
        components:{
            tabbar,orderitem
        },
        created(){
            axios.get(this.$store.state.domain+'order_get_by_keyword',{
                params:{
                    keyword:'outstanding'
                }
            }).then((res)=>{
                this.orders = res.data
                this.Loading = false
            })
        },
        methods:{
            showPopup(item){
                this.nowOrder = item
                this.show = true
                axios.get(this.$store.state.domain+"pack_products_this_order",{
                    params:{
                        orderID:item.id
                    }
                }).then((res)=>{
                    this.packData = res.data
                })
            }
        }
    }
</script>

