// import { createRouter, createWebHistory } from 'vue-router'
import { createRouter, createWebHistory } from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login'
import RegisterUser from '../views/RegisterUser'
import OutstandingOrder from '../views/OutstandingOrder'
import OutstandingOrderEdit from '../views/OutstandingOrderEdit'
import CheckDetailCount from '../views/CheckDetailCount'
import CheckPackDetailCount from '../views/CheckPackDetailCount'
import Setting from '../views/Setting'
import Stock from '../views/Stock'
import CompletedOrder from '../views/CompletedOrder'
import ProductAdd from '../views/ProductAdd'
import DelStock from '../views/DelStock'
import OrderAdd from '../views/OrderAdd'
import OrderEdit from '../views/OrderEdit'
import Pack from '../views/Pack.vue'
import PackDone from '../views/PackDone.vue'
import PackStatusChange from '../views/PackStatusChange'
import StockEdit from '../views/StockEdit.vue'
import axios from 'axios'
import store from '../store'
const routes = [
  {
     path: '/login',
     name: 'Login',
     component: Login
  },
  {
     path: '/register',
     name: 'RegisterUser',
     component: RegisterUser
  },
  {
    path: '/',
    name: 'Home',
    component: Home,
  },
  {
    path: '/outstandingorder',
    name: 'OutstandingOrder',
    component: OutstandingOrder,
  },
  {
    path: '/outstanding_order_edit/:orderid',
    name: 'OutstandingOrderEdit',
    component: OutstandingOrderEdit,
  },
  {
    path: '/setting',
    name: 'Setting',
    component: Setting,
  },
  {
    path: '/stock',
    name: 'Stock',
    component: Stock,
  },
  {
    path: '/stockedit',
    name: 'StockEdit',
    component: StockEdit,
  },
  {
    path: '/completedorder',
    name: 'CompletedOrder',
    component: CompletedOrder,
  },
  {
    path: '/orderadd',
    name: 'OrderAdd',
    component: OrderAdd,
  },
  {
    path: '/orderedit/:orderid',
    name: 'OrderEdit',
    component: OrderEdit,
  },
  {
    path: '/productadd',
    name: 'ProductAdd',
    component: ProductAdd,
  },
  {
    path: '/delstock',
    name: 'DelStock',
    component: DelStock,
  },

  {
    path: '/pack/:orderid/:outstanding',
    name: 'Pack',
    component: Pack,
  },
  {
    path: '/pack/done',
    name: 'PackDone',
   component: PackDone,
  },
  {
    path: '/check_detail_count',
    name: 'CheckDetailCount',
   component: CheckDetailCount,
  },
  {
    path: '/check_pack_detail_count',
    name: 'CheckPackDetailCount',
   component: CheckPackDetailCount,
  },
  {
    path: '/pack/status/change',
    name: 'PackStatusChange',
   component: PackStatusChange,
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})
router.beforeEach((to,from,next)=>{
    //TODO 还需要优化这里
    if (from.name === 'Pack' && to.name === 'Home'){
      console.log("从打包返回主页")
        axios.get(store.state.domain+'order_get_by_id_set_status',{
            params:{
                orderID:from.params.orderid,
                status:0
            }
        }).then((res)=>{
          console.log(res)
        })
        next()
    }
    if (from.name === 'Pack' && to.name === 'OutstandingOrder'){
        console.log("从欠货打包打包返回欠货单主页")
        axios.get(store.state.domain+'order_get_by_id_set_status',{
            params:{
                orderID:from.params.orderid,
                status:2
            }
        }).then((res)=>{
            console.log(res)
        })
        next()
    }


    if (localStorage.getItem('isLogin')!=="true"){
      if (to.name === 'Login'){
        next()
      }
        next('/login')
    }
    return next()
})
export default router
