<template>
  <tabbar :ifactive="0"></tabbar>
  <van-notice-bar
          left-icon="volume-o"
          text="如果点击添加订单没有看到商品，请先去《设置》页面添加商品"
  />
  <van-loading v-if="Loading" size="24px" color="#0094ff" vertical>加载中...</van-loading>
  <div v-else-if="orders.length===0 && !Loading" style="height: 100%;text-align: center;line-height: 400px;font-size: 30px;font-weight: bold">当前没有订单</div>
  <div v-else class="home">
    <van-cell v-for="item in orders" :key="item" :title="'客户:'+item.customer" is-link :value=item.created_at :label="'订单号:'+item.uuid"  @click="showPopup(item)"/>
    <van-popup v-model:show="show" position="right" :style="{ height: '100%',width:'85%' }">
      <orderitem
              :created_at="nowOrder.created_at"
              :customer="nowOrder.customer"
              :content="nowOrder.content"
              :remark="nowOrder.remark"
              :destination="nowOrder.destination"
              :status="nowOrder.status"
              :orderId="nowOrder.id"
              :outstanding="nowOrder.outstanding"
              :packData="packData"
              :page="page"
              :show_os="false"
              :show_pack="false"
              :show_packing="nowOrder.status==1?true:false"
      ></orderitem>
    </van-popup>
  </div>
  <van-button style="position: fixed;bottom: 100px;right: 20px" type="success" round icon="plus" to="/orderadd">添加订单</van-button>
  <div style="padding-bottom: 120px"></div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'
import tabbar from '@/components/Tabbar'
import orderitem from "@/components/OrderItem.vue"
export default {
  name: 'Home',
    data(){
      return{
         orders:[],
         show:false,
         nowOrder:[],
         page:"home",
         Loading:true,
      }
    },
  components: {
      orderitem,
      tabbar
  },
  created(){
      axios.get(this.$store.state.domain+'order_get_by_keyword',{
          params:{
              keyword:'home'
          }
      }).then((res)=>{
          this.orders = res.data
          this.Loading = false
      })
  },
  methods:{
      showPopup(item){
          this.nowOrder = item
          this.show = true
      }
  }
}
</script>
<style scoped>
  .add-order{
    position: fixed;
    bottom: 100px;
    right: 20px;
  }
</style>