<template>
    <stockitem v-for="Product of listData" v-bind:key="Product"
              :detailData="Product.stock"
              :itemName="Product.name"
              :itemId="Product.id"></stockitem>
    <van-button type="success" size="large" @click="submitProduct()">保存库存</van-button>
</template>

<script>
    import stockitem from '@/components/StockItem'
    import axios from 'axios'
    export default {
        name: "stock-edit",
        data(){
            return{
                listData:[],
            }
        },
        components:{
            stockitem,
        },
        created(){
            axios.get(this.$store.state.domain+'product_get',{}).then((res)=>{
                // console.log(res.data)
                this.listData = res.data
                this.$store.state.stockProduct = res.data
            })
        },
        methods:{
            submitProduct(){
                axios.get(this.$store.state.domain+'product_stock_change',{
                    params:{
                        products:this.$store.state.stockProduct
                    }
                }).then((res)=>{
                    console.log(res.data)
                })
            }
        }
    }
</script>

