import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
const app = createApp(App);
//导入初始化样式
import '/src/assets/css/reset.css'
//vant组件导入
import 'vant/lib/index.css'
import { Col,Row,Button,Tabbar,TabbarItem,Tag,Card,NoticeBar,Field,Checkbox,CheckboxGroup,Cell,CellGroup,Popup,ContactCard,SwipeCell,Divider,Loading,Tab,Tabs } from 'vant'
//导入console插件方便移动端调试
import Vconsole from 'vconsole';
let vConsole = new Vconsole();
export default vConsole

//vant组件挂载
app.use(Col);
app.use(Row);
app.use(Button);
app.use(Tabbar);
app.use(TabbarItem);
app.use(Card);
app.use(NoticeBar);
app.use(Field);
app.use(Checkbox);
app.use(CheckboxGroup);
app.use(Cell);
app.use(CellGroup);
app.use(Popup);
app.use(ContactCard);
app.use(SwipeCell);
app.use(Divider);
app.use(Loading);
app.use(Tab);
app.use(Tabs);
app.use(Tag);
//挂在路由，数据仓库功能
app.use(router);
app.use(store);
app.config.productionTip = false

app.mount('#app');
