<template>
    <van-notice-bar
            left-icon="volume-o"
            text="向左滑动可以删除商品"
    />
    <van-swipe-cell v-for="item in listData" v-bind:key="item"  >
        <item :itemName="item.name" :product="item.stock"></item>
        <template #right>
            <van-button @click="delThisStock(item)" style="height: 100%" square type="danger" text="删除" />
        </template>
    </van-swipe-cell>
</template>

<script>
    import axios from "axios"
    import item from "@/components/Item"
    import router from "../router"
    export default {
        name: "del-stock",
        data(){
            return{
                listData:[]
            }
        },
        components:{
            item,
        },
        setup() {
        },
        created(){
            axios.get(this.$store.state.domain+'product_get',{}).then((res)=>{
                // console.log(res.data)
                this.listData = res.data
            })
        },
        methods:{
            delThisStock(item){
                axios.get(this.$store.state.domain+'product_del',{
                    params:{
                        id:item.id
                    }
                }).then((res)=>{
                    if (res.status===200){
                        router.go(0)
                    }else{
                        alert('发生不明原因错误')
                    }
                })
            }
        }
    }
</script>

