<template>
    <enableitem  v-for="product of orderData.outstanding"
                 v-bind:key="product"
                 :itemName="product.name"
                 :detailData="product.stock"
                 :itemId="product.id"></enableitem>

    <van-cell-group>
        <van-field v-model="orderData.customer" label="客户名字" placeholder="请输入客户名字" />
    </van-cell-group>
    <van-cell-group>
        <van-field v-model="orderData.destination" label="送往" placeholder="请输入配送的目的地" />
    </van-cell-group>
    <van-field
            v-model="orderData.remark"
            rows="2"
            autosize
            label="备注"
            type="textarea"
            maxlength="50"
            placeholder="请输入备注，例如：优先配送，加急可以填在这里"
            show-word-limit
    />
    <van-button size="large" type="success" @click="order_edit()">确定修改</van-button>
    <van-button size="large" type="primary" @click="go_back()">返回上级</van-button>
</template>

<script>
    import axios from 'axios'
    import router from '../router'
    import enableitem from '@/components/EnableItem'
    import {Toast} from "vant"
    export default {
        name: "outstanding-order-edit",
        components:{
            enableitem
        },
        data(){
            return{
                orderData:[],
            }
        },
        created(){
            axios.get(this.$store.state.domain+"order_get_by_id",{
                params:{
                    orderID:this.$route.params.orderid
                }
            }).then((res)=>{
                console.log(res.data)
                this.orderData = res.data,
                this.$store.state.PackProduct = res.data.content
            })
        },
        methods:{
            order_edit(){
                console.log(this.orderData.content)
                axios.get(this.$store.state.domain+'/outstanding_order_edit',{
                    params:{
                        orderID:this.orderData.id,
                        outstanding:this.$store.state.PackProduct,
                        customer:this.orderData.customer,
                        destination:this.orderData.destination,
                        remark:this.orderData.remark,
                    }
                }).then((res)=>{
                    if (res.status === 200){
                        Toast('修改成功')
                        router.back()
                    }else{
                        Toast.fail(res.data)
                    }
                })
            },
            go_back(){
                router.back()
            }
        }
    }
</script>

